@import './../../styles/index.scss';

.reviews {
  height: 400px;
  width: 100%;
  overflow: hidden;

  @media screen and (max-width: $screen) {
    max-width: 1000px;
    height: 100%;
    // max-height: fit-content;
  }
}

.swiper-wrapper {
  display: flex;
  align-items: center;
  height: 100%;
}

.swiper-slide {
  padding: 7px;
}

.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  color: $c-link-hover;
  // padding: 30px 26px;
  border-radius: 5px;
  top: 50% !important;
  transform: translateY(-50%) !important;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  --swiper-navigation-size: 30px;
}

.swiper-button-next {
  right: -5px !important;

  @media screen and (max-width: $phone) {
    right: -14px !important;
  }
}

.swiper-button-prev {
  left: -5px !important;

  @media screen and (max-width: $phone) {
    left: -14px !important;
  }
}

.swiper-pagination-bullet-active {
  background-color: #fa0000;
}
