@import './../../styles/index.scss';

.reviewsWrapper {
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url('./../../assets/images/services-bg3.jpg');
  background-position: center;
  background-size: cover;
  animation: position 120s linear infinite;

  @media screen and (max-width: $tablet) {
    animation: position 30s linear infinite;
  }
}

@keyframes position {
  100% {
    background-position: 0 50px;
  }
}

.reviewsContainer {
  background-color: rgba(0, 0, 0, 0.5);
}

.reviews {
  margin-bottom: 40px;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (max-width: $phone) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.title {
  text-align: center;
  font-size: 38px;
  color: $c-white;
  margin-top: 30px;
  margin-bottom: 30px;
  text-transform: uppercase;
  transform: scale(1, 1.2);

  @media screen and (max-width: $phone) {
    font-size: 26px;
  }
}

.subtitle {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 50px;
  color: $c-red;
  text-transform: uppercase;

  @media screen and (max-width: $phone) {
    font-size: 16px;
    margin-bottom: 20px;
  }
}

.imgContainer {
  transform: rotate(180deg);
  overflow: hidden;
}

.imgContainer img {
  display: block;
  overflow: hidden;
}

.imgContainerSecond {
  overflow: hidden;
}

.imgContainerSecond img {
  display: block;
  overflow: hidden;
}

.slide {
  padding: 55px;
  background-color: rgba(12, 12, 12, 0.7);
  backdrop-filter: blur(13px);
  line-height: 26px;
  height: 100%;
  max-height: 350px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: $c-white;
  gap: 15px;

  @media screen and (max-width: $tablet) {
    height: fit-content;
    max-height: 800px;
    padding: 35px;
    font-size: 14px;
  }
}

.stars {
  height: fit-content;
  display: flex;
  gap: 5px;
  align-items: center;
  align-self: start;
}

.iconContainer {
  width: 30px;
  height: 30px;

  @media screen and (max-width: $tablet) {
    width: 20px;
    height: 20px;
  }
}

.name {
  align-self: start;
  font-weight: 800;
  font-size: 20px;

  @media screen and (max-width: $tablet) {
    font-size: 18px;
  }
}
